import React, {useState, useEffect} from 'react';


const ColorPicker = () => {
    const [color, setColor] = useState ('#ffffff')
    useEffect(()=> { 
            document.body.style.background = color
            })  
    return (
        <div className="d-flex container justify-content-center" style={{height:"90vh"}}> 
            <div className= "mx-auto"> 
                <br/>
                <div className="d-flex container justify-content-center my-5 align-items-center" >
                    <label style={{width:"100%"}} for="inputColor"> <h2>Elija un color ☞ </h2> </label> 
                    <input className="d-flex"  type="color" id="inputColor" onChange={(e)=> setColor(e.target.value) }/>
                </div>
            
            <div className="card d-flex justify-content-center my-5">
              <div className="card-body d-flex justify-content-center " >
                 <h5 className="card-title">El color que elegiste es {color}</h5>
                    </div>
                </div>
                </div>
        </div>
    )
    
}

export default ColorPicker
